/* Login.css */

.form-container {
    max-width: 400px;
    margin: 10px auto;
    padding: 20px;
    background-color: #f2f2f2;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .form-container h1 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .form-container .mb-3 {
    margin-bottom: 15px;
  }
  
  .form-container .form-label {
    font-weight: bold;
  }
  
  .form-container .btn-primary {
    width: 100%;
  }