header {
  width: 100%;
  position: sticky;
  top: 0;
  left: 0;
  background-color: var(--primary);
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  padding: 0 32px;
  z-index: 20;
}

.container-logo{
  padding: 15px 0;
}
.container-links{
  display: flex;
  align-items: center;
}

.container-links a, .container-links button{
  padding: 0 5px;
}
header > .container {
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: inherit;
}

header a , header button{
  color: #fff;
  text-decoration: none;
}

header button{
  background: none;
  border: none;
  display: inline ;
  padding: 0 ;
  margin: 0 ;
  border: none ;
  background: none ;
  cursor: pointer ;
  color: inherit ;
}

header h1 {
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  font-size: 1.8rem;
}

@media screen and (min-width: 600px) {
  header > .container {
    flex-direction: row;
    justify-content: space-between;
  }
}
