.form-container {
    margin: auto auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    height: 100%;
  }
  
  .form-container .form-group {
    margin-bottom: 20px;
  }

  .button-sign{
    text-align: center;
    display: block; 
    color: #000;
    cursor: pointer;
    margin-top: 15px;
  }
  
  .form-container label {
    margin-top: 20px;
    font-weight: bold;
  }
  
  .form-container input[type="email"],
  .form-container input[type="password"],
  .form-container input[type="text"],
  .form-container input[type="number"] {
    margin-bottom: 20px;
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .form-container button[type="submit"] {
    width: 100%;
    padding: 10px;
  }
  