.shopping-cart {
    max-width: 400px;
    margin: 10px auto;
    border: 1px solid #ccc;
    border-radius: 8px;
  }
  
  .shopping-cart-header {
    background-color: #f8f9fa;
    font-size: 20px;
    padding: 10px;
    border-bottom: 1px solid #ccc;
    border-radius: 8px 8px 0 0;
  }
  
  .total-values {
    margin-bottom: 10px;
  }
  
  .items-title {
    margin-top: 20px;
    margin-bottom: 10px;
  }
  
  .items-list {
    margin-bottom: 20px;
  }
  
  .item {
    padding: 10px;
    border-radius: 4px;
    background-color: #f8f9fa;
    border: 1px solid #ccc;
    margin-bottom: 10px;
  }
  